<template>
  <div class="container">
    <h2 class="text-center">測驗結果</h2>
    <button class="btn btn-outline-secondary btn-sm mb-3" type="button" @click="$router.push('/examLobby')">返回大廳</button>
    <div class="row">
      <div class="col-12">
        <table v-if="this.examData.TFQ.length !== 0">
          <tr>
            <td colspan="4" class="h2 text-center">是非</td>
          </tr>
          <tr>
            <td class="h4">#</td>
            <td class="h4">正確</td>
            <td class="h4">作答</td>
            <td class="h4">題目</td>
          </tr>
          <tr v-for="(item, index) in examData.TFQ" :id="item.QID" :style="item.result ? 'background-color: #cfffcf' : 'background-color: #ff7d7a'">
            <td class="fw-bolder">{{ index + 1 }}</td>
            <td class="text-center">{{ item.correct }}</td>
            <td class="text-center" :class="item.result ? 'text-success' : 'text-danger fw-bold'">{{ item.answer }}</td>
            <td>{{ item.content }}</td>
          </tr>
        </table>
        <table v-if="this.examData.SCQ.length !== 0">
          <tr>
            <td colspan="4" class="h2 text-center">單選</td>
          </tr>
          <tr>
            <td class="h4">#</td>
            <td class="h4">正確</td>
            <td class="h4">作答</td>
            <td class="h4">題目</td>
          </tr>
          <tr v-for="(item, index) in examData.SCQ" :id="item.QID" :style="item.result ? 'background-color: #cfffcf' : 'background-color: #ff7d7a'">
            <td>{{ index + 1 }}</td>
            <td class="text-center">{{ item.correctIndex }}</td>
            <td class="text-center" :class="item.result ? 'text-success' : 'text-danger fw-bold'">{{ item.answerIndex }}</td>
            <td>
              <p class="fw-bold">{{ item.content }}</p>
              <p v-for="(option, i) in item.options" class="d-inline mx-2">{{ i + 1 }}.{{ item.options[i] }}</p>
            </td>
          </tr>
        </table>
        <table v-if="this.examData.MCQ.length !== 0">
          <tr>
            <td colspan="4" class="h2 text-center">多選</td>
          </tr>
          <tr>
            <td class="h4">#</td>
            <td class="h4">正確</td>
            <td class="h4">作答</td>
            <td class="h4">題目</td>
          </tr>
          <tr v-for="(item, index) in examData.MCQ" :id="item.QID" :style="item.result ? 'background-color: #cfffcf' : 'background-color: #ff7d7a'">
            <td>{{ index + 1 }}</td>
            <!-- 陣列取出數字成為字串 -->
            <td>{{ item.correctIndex.join() }}</td>
            <td :class="item.result ? 'text-success' : 'text-danger fw-bold'">{{ item.answerIndex.join() }}</td>
            <td class="left">
              <p class="fw-bold">{{ item.content }}</p>
              <p v-for="(option, i) in item.options" class="d-inline mx-2">{{ i + 1 }}.{{ item.options[i] }}</p>
            </td>
          </tr>
        </table>
      </div>
      <div class="col-12 mt-3">
        <h4 class="text-danger text-center">{{ resultText }}</h4>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: "ExamResult",
  components: {},
  data() {
    return {
      resultText: "", // 測驗結果提示文字
      examData: {
        TFQ: [],
        SCQ: [],
        MCQ: [],
      },
    };
  },
  methods: {},
  created() {
    // 取得考試結果
    this.examData = this.$store.state.examination;
    // 統計題目錯誤數量
    // 統計是非題result為false的數量
    const TFQWrong = this.examData.TFQ.filter((item) => item.result === false).length;
    // 統計單選題result為false的數量
    const SCQWrong = this.examData.SCQ.filter((item) => item.result === false).length;
    // 統計複選題result為false的數量
    const MCQWrong = this.examData.MCQ.filter((item) => item.result === false).length;
    // 判斷考試模式並顯示提示文字
    if (this.examData.examMode === "K書模式" || this.examData.examMode === "弱點測驗") {
      if (this.examData.examType === "準則") {
        this.resultText = `單選錯${SCQWrong}題，多選錯${MCQWrong}題`;
      } else {
        this.resultText = `是非錯${TFQWrong}題，單選錯${SCQWrong}題，多選錯${MCQWrong}題`;
      }
    } else {
      if (this.examData.examType === "準則") {
        // 準則計算分數[單選1分、多選2分]
        this.resultText = `你的成績為：${100 - (SCQWrong + MCQWrong * 2)}分`;
      }
      // 學科計算分數[是非1分、單選1分、多選2分]
      this.resultText = `你的成績為：${100 - (TFQWrong + SCQWrong + MCQWrong * 2)}分`;
    }
  },
  mounted() {
    // 清除Vuex考試資料
    this.$store.commit("SET_EXAMINATION", {});
  },
  computed: {},
  watch: {},
};
</script>

<style lang="scss">
p {
  margin: 0;
}
table {
  border: 5px double black;
  margin: auto;
  width: 100%;
}
td {
  border: 1px solid black;
}
</style>
